<template>
	<div class="index">
		<div class="top">
			<div class="progress">
				<el-progress type="circle" :percentage="20.72" :stroke-width='10' :width='316' :show-text='true'></el-progress>
				<p>流通股占比</p>
			</div>
			<div class="list">
				<el-row>
					<el-col :span='8'>
						<P class="first">10000 份</P>
						<P class="two">总股数</P>
					</el-col>

					<el-col :span='8'>
						<P class="first">2072 份</P>
						<P class="two">流通股</P>
					</el-col>

					<el-col :span='8'>
						<P class="first">7928 份</P>
						<P class="two">非流通股</P>
					</el-col>
				</el-row>
			</div>
			<div class="btn" v-if='showOutstand'>
				<el-button type="primary" @click='shareOutstand'>设置流通股数</el-button>
			</div>
			<div class="btn" v-if='showEquity'>
				<el-button type="primary" @click='equityRegister'>股权登记</el-button>
			</div>
		</div>
		<div class="center">
			<div class="left">
				<p>我的股份数</p>
				<p v-loading="loading1">{{ balance }}</p>
			</div>
			<div class="right" v-if='transfer'>
				<el-button type="primary" @click='noTransfer'>非交易过户</el-button>
			</div>
		</div>
		<div class="bottom">
			<el-row>
				<el-col :span='6'>
					<p @click='myAddress'>我的账户地址</p>
				</el-col>
				<el-col :span='6'>
					<P @click='registerRecord'>登记记录</P>
				</el-col>
				<el-col :span='6'>
					<P @click='turnRecord'>转出记录</P>
				</el-col>
				<el-col :span='6'>
					<P @click='turnInRecord'>转入记录</P>
				</el-col>
			</el-row>
		</div>
		<!-- 弹窗 -->
		<div class="modal" v-if="box">
			<div class="modal-main user-modal" v-loading='loading'>
				<el-row class="modal-title">
					<span>设置流通股数</span>
				</el-row>
				<div class="modal-content">
					<p>
						<span>流通股数</span>
						<span class="height input-special">
                            <input type="text"  placeholder='2072'  value='2072' readonly>
                        </span>

					</p>
					<p>
						<span v-show='success' class='size-model-green'>设置流通股数成功！</span>
						<span v-show='fail' class='size-model'>设置流通股数失败！</span>
					</p>
					<p>
						<button @click="cancel">取消</button>
						<button @click="sure" class='sure'>确认</button>
					</p>
				</div>
			</div>
		</div>

		<div class="modal" v-if="info.flag">
			<div class="modal-main">
				<div class="fail-content">
					<span>
						{{info.text}}
					</span>
				</div>
				<div class="fail-bottom" @click='failUser'>
					确定
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import ContractServies from '@/services/contract-servies';
	import ContractConfig from '@/config/contract-config';

	export default {
		//组件名
		name: 'index',
		//实例的数据对象。
		data() {
			return {
				success: false, //设置流通股数成功
				fail: false, //设置流通股数失败
				showOutstand: false, //初始
				showEquity: false, //非初始
				box: false, //弹窗
				visible: false, //x
				balance: 0, //股份数
				transfer: false, //非交易过户
				cipherText: '', //密文
				loading1: false, //我的股份数以及非交易用户的按钮遮罩层isAdmin:this.admin
				admin: "", //是不是管理员
				loading: false, //弹窗确认加载
				info: {
					flag: false,
					text: '',
				}
			}
		},
		//数组或对象，用于接收来自父组件的数据
		props: {

		},
		//计算
		computed: {

		},
		//方法
		methods: {
			failUser() {
				this.info.flag = false;
			},
			//判断是不是管理员，是则继续判断是否需要初始化
			shareOutstand() {
				this.box = true;
			},
			//取消弹窗 关闭弹窗
			cancel() {
				this.box = false;
			},
			//跳转到登记记录
			registerRecord() {
				console.log('跳转到登记记录');
				this.$router.push({
					path: '/welcome/registerRecord',
					query: {
						account: ContractServies.getAddress(),
						admin: this.admin
					}
				});
			},
			//跳转到转出记录
			turnRecord() {
				console.log('跳转到转出记录');
				this.$router.push({
					path: '/welcome/turnRecord',
					query: {
						account: ContractServies.getAddress(),
						admin: this.admin
					}
				});
			},
			//跳转到转入记录
			turnInRecord() {
				console.log('跳转到转入记录');
				this.$router.push({
					path: '/welcome/turnInRecord',
					query: {
						account: ContractServies.getAddress(),
						admin: this.admin
					}
				});
			},
			//非交易过户跳转
			noTransfer() {
				console.log('扫地址');
				Was.exec('Qrcode', 'openDevice', '', (res) => {
					if(res.code == 0) {
						console.log('res.data' + res.data);
						console.log('非交易过户扫到的二维码' + res.data);
						if(res.data.length != 42) {
							//							alert('扫描失败！');
							this.info = {
								flag: true,
								text: '扫描失败',
							};

						} else {
							//根据扫到的地址获取转入公钥
							let response = ContractServies.call('JuzixTokenManager', 'getPubkey', [res.data]);
							console.log('获取公钥>>>>>>>>>>' + response);
							if(response.pubKey) {
								this.$router.push({
									path: '/welcome/noTransfer',
									query: {
										address: res.data,
										balance: this.balance,
										cipherText: this.cipherText,
										publicKey: response.pubKey
									}
								});
							} else {
								this.info = {
									flag: true,
									text: '获取公钥失败',
								};
							}
						}
					}
				});

			},
			//股权登记
			equityRegister() {
				console.log('扫地址');
				Was.exec('Qrcode', 'openDevice', '', (res) => {
					if(res.code == 0) {
						console.log('res.data' + res.data)
						console.log('股权登记扫到的二维码' + res.data);
						if(res.data.length != 42) {
							//							alert('扫描失败！');
							this.info = {
								flag: true,
								text: '扫描失败',
							};
						} else {
							//根据扫到的地址获取转入公钥
							let response = ContractServies.call('JuzixTokenManager', 'getPubkey', [res.data]);
							console.log('获取公钥>>>>>>>>>>' + JSON.stringify(response));
							if(response.pubKey) {
								this.$router.push({
									path: '/welcome/equityRegister',
									query: {
										address: res.data,
										balance: this.balance,
										cipherText: this.cipherText,
										publicKey: response.pubKey
									}
								});
							} else {
								this.info = {
									flag: true,
									text: '获取公钥失败',
								};
							}
						}
					}
				});
			},

			//确认弹窗 关闭弹窗
			sure() {
				this.loading = true;
				let obj = {
					publicKey: ContractServies.getpublicKey(),
					message: '2072'
				};
				//设置流通股数 加密+交易
				Was.exec('nizk', 'nizkEncryption', obj, (res) => {
					if(res.code == 0) {
						console.log('加密流通股数' + res.data);
						ContractServies.sendRawTrasaction('JuzixTokenManager', 'setCirculationShares', [res.data], '', (ret, data) => {
							if(ret == 0) {
								console.log('设置流通股数成功');

								this.getBalance();
								this.success = true;
								this.loading = false;
								this.showOutstand = false;
								this.showEquity = true;
								this.box = false;
							} else {
								//								alert(data);
								this.fail = true;
								this.loading = false;
								this.showOutstand = true;
								this.showEquity = false;
								this.box = false;
							}
						});
					} else {
						this.fail = true;
						this.loading = false;
						//						alert('加密失败');
					}
				});
			},
			//生成二维码
			myAddress() {
				//生成二维码时，是公钥加用户地址
				let content = ContractServies.getAddress();
				let obj = {
					content: content
				};
				Was.exec('Qrcode', 'getQrcode', obj, (res) => {
					if(res.code == 0) {
						console.log('生成二维码' + res.data);
						this.$router.push({
							path: '/welcome/address',
							query: {
								img: res.data
							}
						});
					}
				});
			},
			//查询股份数
			getBalance() {
				this.loading1 = true;
				let res = ContractServies.call('JuzixTokenManager', 'balanceOfPaillier', [ContractServies.getAddress()]),
					nizkDecryption = () => {
						//解密余额
						this.cipherText = res.balance;
						//解密
						let obj = {
							ciphertext: res.balance,
							privateKey: ContractServies.getprivateKeyRandom()
						};
						console.log('obj',obj);
						Was.exec('nizk', 'nizkDecryption', obj, (res) => {
							this.loading1 = false;
							if(res.code == 0) {
								console.log('解密成功' + res.data);
								console.log(res.data);
								this.balance = res.data == "" ? 0 : res.data;

								this.transfer = res.data == 0 ? false : true;
							} else {
								//								alert('解密失败');
								this.info = {
									flag: true,
									text: '解密失败',
								};
							}
						});
					};

				console.log('获取余额和公钥>>>>>>>>>>' + JSON.stringify(res));
				//				if(res.balance !== '') {
				//判断公钥是否存在
				if(res.pubKey) {
					nizkDecryption();
				} else {
					console.log('开始设置公钥')
					//设置公钥
					console.log('同态公钥'+ContractServies.getpublicKey());
					ContractServies.sendRawTrasaction('JuzixTokenManager', 'setPubkey', [ContractServies.getpublicKey()], '', (ret, data) => {
						if(ret == 0) {
							console.log('设置公钥成功');
							nizkDecryption();
						} else {
							this.info = {
								flag: true,
								text: '设置公钥失败',
							};
						}
					});

				}
			
			},
			callContract() {
				//获取管理员和是否初始化
				let response = ContractServies.call('JuzixTokenManager', 'isAdmin', []);
				console.log('获取是否管理员>>>>>>>>>>' + JSON.stringify(response));
				if(response) {
					//判断是否为管理员
					this.admin = response.isAdmin;
					if(response.isAdmin == 1) {
						//判断是否需要初始化
						if(response.circulationShares !== '') {
							//非初始化
							this.showEquity = true;
							this.getBalance();
						} else {
							//初始化
							this.showOutstand = true;
						}
					} else {
						this.getBalance();
					}
				};
			}
		},
		//生命周期函数
		created() {
			this.callContract();
		},
		beforeMount() {

		},
		mounted() {

		},
		//监视
		watch: {
			"balance": "getBalance",
		},
		//组件
		components: {

		},
		//过滤器
		filters: {

		},
		//自定义指令
		directive: {

		}
	}
</script>

<!--
    作者：liangyanxiangde@163.com
    时间：2017-03-27
    描述：统一使用less,局部样式
-->
<style lang="less" scoped>
	@import "../../less/modules/color.less";
	@fontColor: #fff;
	@height: 1rem;
	.top {
		background: -webkit-linear-gradient(left, #4c1fff 0%, #0383ff 100%);
		padding: .32rem 0 .56rem;
		.progress {
			// height:3.2rem;
			padding: 0 .66rem;
			text-align: center;
			p {
				// margin-bottom:1.3rem;
				margin-top: -1rem;
				color: @fontColor;
				font-size: .24rem;
			}
		}
		.list {
			padding: .9rem .66rem .40rem;
			color: @fontColor;
			.el-col {
				text-align: center;
				background: url(images/line.png) no-repeat right center;
				&:last-child {
					background: none;
				}
				.first {
					margin-bottom: .10reme;
					font-size: .30rem;
				}
				.two {
					font-size: .24rem;
				}
			}
		}
		.btn {
			padding: 0 .30rem;
			.el-button {
				width: 100%;
				height: .78rem;
				background-color: @fontColor;
				color: #3a8bef;
				font-size: .30rem;
				border-radius: .10rem;
				&:hover {
					background-color: #00a2ff;
					color: @fontColor;
				}
			}
		}
	}
	
	.center {
		padding: .87rem .14rem 1.59rem .64rem;
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
		.left {
			/*margin-right: 2.62rem;*/
			float: left;
			p {
				&:first-child {
					color: #333;
					font-size: .28rem;
					margin-bottom: .30rem;
				}
				&:last-child {
					color: #000;
					font-size: .46rem;
				}
			}
		}
		.right {
			margin-top: .30rem;
			.el-button {
				width: 1.70rem;
				height: .58rem;
				padding: .08rem;
				font-size: .28rem;
				background-color: #3c8bed;
				border-radius: 0;
			}
		}
	}
	
	.bottom {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 0rem .26rem;
		color: #6f6f6f;
		margin-bottom: .30rem;
		.el-col {
			text-align: center;
		}
		p {
			cursor: pointer;
			font-size: .26rem;
			line-height: .3rem;
			padding: .5rem 0;
		}
	}
	
	.modal {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		.modal-main {
			//弹窗主体
			position: absolute;
			// width:50%;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			background-color: #fff;
			border-radius: .18rem;
			.modal-title {
				//弹窗标题
				height: 1rem;
				line-height: 1rem;
				text-align: center;
				border-bottom: .01rem solid @border3;
			}
			.modal-content {
				// height:250rem/@base;
				// background:red;
				// border-bottom:1px solid @border3;
				p {
					&:first-child {
						border-bottom: 1px solid @border3;
						display: flex;
						flex-wrap: nowrap;
						span {
							display: inline-block;
							color: #000;
							&:last-child {
								width: 4.30rem;
								word-wrap: break-word;
								word-break: break-all;
							}
							&:first-child {
								width: 1.50rem;
								height: @height;
								line-height: @height;
								margin-right: .20rem;
								font-size: .28rem;
							}
						}
						.input-special {
							position: relative;
							i {
								display: inline-block;
								width: .60rem;
								line-height: @height;
								position: absolute;
								right: 0;
								top: 0;
								font-style: normal;
								font-size: .30rem;
								text-align: center;
							}
							input {
								display: inline-block;
								border: 0;
								height: 100%;
								line-height: @height;
								// background:red;
								width: 100%;
								font-size: .24rem;
							}
						}
					}
					&:nth-child(2) {
						height: .6rem;
						line-height: .6rem;
						/*background:red;*/
					}
					&:last-child {
						/*background:red;*/
						text-align: right;
						margin-top: 0rem;
						button {
							// width:1rem;
							padding: 0 .3rem;
							height: .80rem;
							line-height: .80rem;
							background: none;
							border-radius: .12reme;
							border: none;
							margin: 0 .20rem;
							color: #000;
							font-size: .26rem;
							text-align: center;
						}
						.sure {
							/*background: #0383ff;*/
							color: #0383ff;
						}
					}
				}
			}
			.fail-content {
				padding: .5rem 1rem .2rem;
				font-size: .3rem;
				border-bottom: .02rem solid @border3;
				color: #0383ff;
			}
			.fail-bottom {
				font-size: .28rem;
				text-align: center;
				height: 1rem;
				line-height: 1rem;
				color: #ccc;
			}
		}
	}
	
	.user-modal {
		width: 6.10rem;
		height: 3.50rem;
		padding: 0 .24rem;
	}
	
	.size-model {
		height: .3rem;
		font-size: .28rem;
		color: red;
		line-height: .3rem;
		;
	}
	
	.size-model-green {
		height: .4rem;
		font-size: .28rem;
		color: #0383ff;
		line-height: .4rem;
	}
</style>


